    text-align: center;
}

#ship-options {
    width: 50%;
    margin-top: 20px;
    display: flex;
    gap: 2em;
    justify-content: center;
    position: relative;
    transform: translate(50%, 0%);
    left: 0%;
    flex-wrap: wrap;
}

